import {NgModule} from '@angular/core';

import {AUTH_SERVICE, AuthModule as NgxAuthModule, PROTECTED_FALLBACK_PAGE_URI, PUBLIC_FALLBACK_PAGE_URI} from 'ngx-auth';
import {TokenStorage} from './services/token-storage.service';
import {AuthRoutingModule} from './auth-routing.module';
import {LoginComponent} from './login/login.component';
import {RegisterComponent} from './register/register.component';
import {AuthenticationService} from './services/authentication.service';
import {SharedModule} from '../shared/shared.module';
import {CoreModule} from '../core/core.module';
import {ConfirmDialogComponent} from '../core/components/dialog/confirm-dialog/confirm-dialog.component';

export function factory(authenticationService: AuthenticationService) {
  return authenticationService;
}

@NgModule({
  imports: [
    CoreModule,
    NgxAuthModule,
    AuthRoutingModule,
  ],
  providers: [
    TokenStorage,
    AuthenticationService,
    {provide: PROTECTED_FALLBACK_PAGE_URI, useValue: '/'},
    {provide: PUBLIC_FALLBACK_PAGE_URI, useValue: '/login'},
    {
      provide: AUTH_SERVICE,
      deps: [AuthenticationService],
      useFactory: factory
    }
  ],
  declarations: [LoginComponent, RegisterComponent],
  entryComponents: [ConfirmDialogComponent]
})
// @ts-ignore
export class AuthModule {
}
