import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {RequestService} from '../../../services/request.service';
import {Center, Comment, Patient, Request} from '../../../../core/model/model';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {PatientService} from '../../../../core/services/patient.service';
import {TranslateService} from '@ngx-translate/core';
import {CenterService} from '../../../../core/services/center.service';
import {ConfigurationService} from '../../../../core/services/configuration.service';
import {LoadingService} from '../../../../core/services/loading.service';
import {MatDialog} from '@angular/material';
import {RequiredIfOtherControlHasValue, RequiredIfOtherFieldHasValue} from '../../../../core/validators/requiredIfOtherFIeldHasValue';
import {SpeedDialFabPosition} from '../../../../core/components/speed-dial-fab/speed-dial-fab.component';
import {Location} from '@angular/common';
import {PatientCreateComponent} from '../../patient/patient-create/patient-create.component';
import {RequestAskInformationsComponent} from '../../../components/request-ask-informations/request-ask-informations.component';
import {RequestSelectPopulationComponent} from '../../../components/request-select-population/request-select-population.component';
import {ConfirmDialogComponent} from '../../../../core/components/dialog/confirm-dialog/confirm-dialog.component';
import {RequestCloseComponent} from '../../../components/request-close/request-close.component';
import {RequestCommentsComponent} from '../../../components/request-comments/request-comments.component';

@Component({
  selector: 'app-request-edit',
  templateUrl: './request-edit.component.html',
  styleUrls: ['./request-edit.component.scss']
})
export class RequestEditComponent implements OnInit {
  request: Request;
  errorMessage: String;
  speedDialFabPosition = SpeedDialFabPosition.Bottom;

  speedDialFabButtons = [
    {
      icon: 'settings',
      tooltip: 'request.button.launchCalculation',
      clickHandler: () => {
        const dialogRef = this.dialog.open(RequestSelectPopulationComponent, {data: {requestId: this.request.id}});
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.loadRequest(this.request.id);
          }
        });
      }
    },
    {
      icon: 'comment',
      tooltip: 'request.button.askInformation',
      clickHandler: () => {
        const dialogRef = this.dialog.open(RequestAskInformationsComponent, {data: {requestId: this.request.id}});
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.location.back();
          }
        });
      }
    },
    {
      icon: 'check',
      tooltip: 'request.button.validate',
      clickHandler: () => {
        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
          data: {
            confirm: true,
            title: 'request.validate.title',
            content: 'request.validate.content'
          }
        });
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.loadingService.show();
            this.requestService.validateRequest(this.request.id, null).subscribe(
              request => {
                this.location.back();
                this.loadingService.hide();
              },
              error => {
                this.loadingService.hide();
                this.errorMessage = error.error.description;
              }
            );
          }
        });
      }
    },
    {
      icon: 'block',
      tooltip: 'request.button.close',
      clickHandler: () => {
        const dialogRef = this.dialog.open(RequestCloseComponent, {data: {requestId: this.request.id}});
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            this.location.back();
          }
        });
      }
    },
    {
      icon: 'backspace',
      tooltip: 'request.button.back',
      clickHandler: () => this.location.back()
    }
  ];

  constructor(private route: ActivatedRoute, private _formBuilder: FormBuilder, private patientService: PatientService,
              private translateService: TranslateService, private centerService: CenterService, private configurationService: ConfigurationService,
              private requestService: RequestService, private loadingService: LoadingService, private location: Location,
              private translate: TranslateService, public dialog: MatDialog, private router: Router) {

  }

  ngOnInit() {
    this.route.params.subscribe(
      params => {
        const id = params['id'];
        this.loadRequest(id);
      });


  }

  openComments(){
    this.dialog.open(RequestCommentsComponent, {data: {comments: this.request.comments}});
  }


  private loadRequest(id: string) {
    this.loadingService.show();
    this.requestService.getRequest(id).subscribe((request) => {
      this.loadingService.hide();
      this.request = request;
    });
  }

}
