export const WILD_CARD = '*';

export class DrugCommercialName {
  drugCommercialName: string;
  sampling: Array<string>;
}

export class Drug {
  drug: string;
  drugCommercialNames: Array<DrugCommercialName>;
}

export class RouteOfAdministration {
  roa: string;
  drugs: Array<Drug>;
}

export class Disease {
  disease: string;
  roas: Array<RouteOfAdministration>;
}


export const AGE_LT18 = 'LT_18';
export const AGE_GT18 = 'GT_18';

export class Age {
  age: string;
  diseases: Array<Disease>;
}

export class ConfigurationIndication {
  indication: string;
  ages: Array<Age>;
}

export class Sampling {
  sampling: string;
  indications: Array<ConfigurationIndication>;
}

export class RequestConfiguration {
  sampling: Sampling;
}
