import {ChangeDetectorRef, Component, Inject, OnInit, Optional} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Address, Center, Consent, ConsentType, Location, Patient, ValueSetEntry} from '../../../../core/model/model';
import {GooglePlaceService} from '../../../../core/services/google-place.service';
import {Router} from '@angular/router';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {CenterService} from '../../../../core/services/center.service';
import {TranslateService} from '@ngx-translate/core';
import {LoadingService} from '../../../../core/services/loading.service';
import {ConfirmDialogComponent} from '../../../../core/components/dialog/confirm-dialog/confirm-dialog.component';
import {PatientService} from '../../../../core/services/patient.service';
import {Location as LocationBrowser} from '@angular/common';
import {RequiredIfOtherFieldHasValue} from '../../../../core/validators/requiredIfOtherFIeldHasValue';
import {ValueSetService} from '../../../../core/services/value-set.service';
import {merge} from 'lodash';

@Component({
  selector: 'app-patient-create',
  templateUrl: './patient-create.component.html',
  styleUrls: ['./patient-create.component.scss']
})
export class PatientCreateComponent implements OnInit {

  form: FormGroup;
  errorMessage: String;
  private place: google.maps.places.PlaceResult;
  centers: Array<Center> = [];
  genders = [{code: 'M', vsCode: 'GENDER'}, {code: 'F', vsCode: 'GENDER'}, {code: 'O', vsCode: 'GENDER'}];
  private data: any;
  isModal: boolean;
  private patient = new Patient();
  indicationsCtrl: FormArray;
  indications: Array<ValueSetEntry>;

  constructor(private googlePlaceService: GooglePlaceService, private router: Router, private cdr: ChangeDetectorRef, public dialog: MatDialog,
              private fb: FormBuilder, private centerService: CenterService, private patientService: PatientService,
              private translateService: TranslateService, private loadingService: LoadingService,
              @Optional() private dialogRef: MatDialogRef<PatientCreateComponent>,
              @Optional() @Inject(MAT_DIALOG_DATA) data = null, private location: LocationBrowser, private valueSetService: ValueSetService) {
    this.data = data;
    if (this.dialogRef && this.data) {
      this.isModal = true;
    }
  }

  ngOnInit() {
    this.form = this.fb.group({
      gender: ['', Validators.required],
      lastName: [''],
      birthName: ['', Validators.required],
      firstName: ['', Validators.required],
      birthDate: ['', Validators.required],
      birthRank: ['', Validators.required],
      externalId: [''],
      nationalId: ['', [Validators.required, Validators.pattern('(1|2)[0-9]{12}')]],
      email: ['', [Validators.email]],
      center: ['', Validators.required],
      medicalInformation: this.fb.group({
        indications: this.fb.array([]),
      }),
      healthInsurance: this.fb.group({
        codeOrganisation: ['']
      }),
      address: this.fb.group({
        addressComplete: [''],
        number: [''],
        street: [''],
        additional: [''],
        postalCode: ['', Validators.required],
        city: ['', Validators.required],
        location: this.fb.group({
          lat: '',
          lng: '',
        }),
        country: ['', Validators.required],
      }),
      consentTeleexpertise: [''],
      consentTelemonitoring: ['']
    });
    this.indicationsCtrl = this.form.get('medicalInformation.indications') as FormArray;
    this.indicationsCtrl.push(this.createIndication());

    this.loadData();
  }


  loadData() {
    if (this.isModal && this.data.center) {
      this.centers = [this.data.center];
      this.patient.center = this.data.center;
    } else {
      this.centerService.getCenters().subscribe(centers => {
        this.centers = centers;
      });
    }
    this.form.patchValue(this.patient);
    this.valueSetService.getValues('INDICATION').subscribe(values => this.indications = values);
  }


  getErrorMessage(field: string) {
    return this.form.get(field).hasError('required') ? this.translateService.instant('form.field.required') :
      this.form.get(field).hasError('email') ? this.translateService.instant('form.field.emailInvalid') :
        '';
  }


  // ***********************************************************
  // *******************        Action   ***********************
  // ***********************************************************
  cancel() {
    if (this.isModal) {
      this.dialogRef.close();
    } else {
      this.location.back();
    }
  }

  onSubmit() {
    if (this.form.valid) {
      merge(this.patient, this.form.getRawValue());
      if (this.patient['consentTeleexpertise']) {
        const consent = new Consent();
        consent.type = ConsentType.TELE_EXPERTISE;
        consent.startDate = new Date();
        this.patient.medicalInformation.consents.push(consent);
        delete this.patient['consentTeleexpertise'];
      }
      if (this.patient['consentTelemonitoring']) {
        const consent = new Consent();
        consent.type = ConsentType.TELE_MONITORING;
        consent.startDate = new Date();
        this.patient.medicalInformation.consents.push(consent);
        delete this.patient['consentTelemonitoring'];
      }
      this.loadingService.show();
      this.patientService.save(this.patient as Patient).subscribe(
        value => {
          this.loadingService.hide();
          if (this.isModal) {
            this.dialogRef.close(value);
          } else {
            const dialogRef = this.dialog.open(ConfirmDialogComponent, {
              data: {
                title: this.translateService.instant('patient.create.confirm.title'),
                content: this.translateService.instant('patient.create.confirm.content')
              }
            });

            dialogRef.afterClosed().subscribe(result => {
              this.router.navigateByUrl('/teleexpertise/patients');
            });
          }
        },
        error => {
          this.loadingService.hide();
          this.errorMessage = error.error.message;
        }
      );
    }
  }

  // ***********************************************************
  // *******************FormGroup creator***********************
  // ***********************************************************

  createIndication(): FormGroup {
    return this.fb.group({
      date: ['', Validators.required],
      code: ['', Validators.required],
      comment: ['', RequiredIfOtherFieldHasValue('code', 'A_OTHER')],
      vsCode: ['INDICATION', Validators.required],
    });
  }

  addIndication(): void {
    this.indicationsCtrl.push(this.createIndication());
  }

  removeIndication(index: number): void {
    this.indicationsCtrl.removeAt(index);
  }


  getIndicationDateLabel(index: number) {
    if (this.indicationsCtrl.at(index).get('code').value.startsWith('T')) {
      return this.translateService.instant('patient.medicalInformation.indication.transplantation.date') + '*';
    } else {
      return this.translateService.instant('patient.medicalInformation.indication.treatment.date') + '*';
    }
  }


  // ***********************************************************
  // *******************Change event***********************
  // ***********************************************************
  setAddressOnChange(place: google.maps.places.PlaceResult) {
    this.place = place;
    if (this.place) {
      // set latitude, longitude and zoom
      const address: Address = new Address();
      address.location = new Location();
      address.location.lat = this.googlePlaceService.latitude(this.place);
      address.location.lng = this.googlePlaceService.longitude(this.place);
      address.number = this.googlePlaceService.street_number(this.place);
      address.street = this.googlePlaceService.street(this.place);
      address.postalCode = this.googlePlaceService.postal_code(this.place);
      address.city = this.googlePlaceService.city(this.place);
      address.postalBox = this.googlePlaceService.post_box(this.place);
      const centerName = this.googlePlaceService.name(this.place);
      address.additional = this.googlePlaceService.floor(this.place);
      address.cedex = this.googlePlaceService.postal_town(this.place);
      address.country = this.googlePlaceService.country(this.place);
      address['adresseComplete'] = null;
      this.form.get('address').patchValue(address);
    } else {
      this.form.get('address').get('location').setValue(new Location());
    }
    this.cdr.markForCheck();
  }


}
