import {Injectable} from '@angular/core';
import {HttpClient, HttpErrorResponse, HttpHandler, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import {catchError, map, switchMap, tap} from 'rxjs/operators';
import {AuthService} from 'ngx-auth';

import {TokenStorage} from './token-storage.service';
import {environment} from '../../../environments/environment';
import {User} from '../../core/model/model';

interface AccessData {
  access_token: string;
  refreshToken: string;
}

@Injectable()
export class AuthenticationService implements AuthService {

  constructor(
    private http: HttpClient,
    private tokenStorage: TokenStorage
  ) {
  }

  /**
   * Check, if user already authorized.
   * @description Should return Observable with true or false values
   * @returns {Observable<boolean>}
   * @memberOf AuthService
   */
  public isAuthorized(): Observable<boolean> {
    return this.tokenStorage
      .getAccessToken()
      .pipe(map(token => !!token));
  }

  /**
   * Get access token
   * @description Should return access token in Observable from e.g.
   * localStorage
   * @returns {Observable<string>}
   */
  public getAccessToken(): Observable<string> {
    return this.tokenStorage.getAccessToken();
  }

  /**
   * Function, that should perform refresh token verifyTokenRequest
   * @description Should be successfully completed so interceptor
   * can execute pending requests or retry original one
   * @returns {Observable<any>}
   */
  public refreshToken(): Observable<AccessData> {
    return this.tokenStorage
      .getRefreshToken()
      .pipe(
        switchMap((refreshToken: string) =>
          this.http.post(`http://localhost:3000/refresh`, {refreshToken})
        ),
        tap((tokens: AccessData) => this.saveAccessData(tokens)),
        catchError((err) => {
          this.logout();

          return Observable.throw(err);
        })
      );
  }

  /**
   * Function, checks response of failed request-list to determine,
   * whether token be refreshed or not.
   * @description Essentialy checks status
   * @param {Response} response
   * @returns {boolean}
   */
  public refreshShouldHappen(response: HttpErrorResponse): boolean {
    return response.status === 401;
  }

  /**
   * Verify that outgoing request-list is refresh-token,
   * so interceptor won't intercept this request-list
   * @param {string} url
   * @returns {boolean}
   */
  public verifyTokenRequest(url: string): boolean {
    return url.endsWith('/refresh');
  }

  /**
   * EXTRA AUTH METHODS
   */

  public login(login: string, password: string, otp: string = null): Observable<any> {
    // return new Observable(subscriber => {
    //   if(login!=="test" && password!="care33") {
    //     subscriber.error("Identifiant ou mot de passe invalide")
    //   }  else if(!otp){
    //     subscriber.error("OTP")
    //   } else if(otp && otp!=="1234"){
    //     subscriber.error("Code otp invalide")
    //   } else {
    //     subscriber.next({accessToken: '1651561231532', refreshToken:'556161651561651'});
    //     subscriber.complete();
    //   }
    // }).pipe(tap((tokens: AccessData) => this.saveAccessData(tokens)));

    const params = new URLSearchParams();
    params.append('username', login);
    params.append('password', password);
    params.append('otp', otp);
    params.append('grant_type', 'password');
    params.append('client_id', 'optimis-frontend');
    const headers = new HttpHeaders({
      'Content-type': 'application/x-www-form-urlencoded; charset=utf-8',
      'Authorization': 'Basic ' + btoa('optimis-frontend:secret')
    });

    return this.http.post(environment.oauth_url + '/oauth/token', params.toString(), {headers: headers})
      .pipe(tap((tokens: AccessData) => this.saveAccessData(tokens)));
  }


  /**
   * Logout
   */
  public logout(): void {
    this.tokenStorage.clear();
    location.replace('/teleexpertise/home');
    // location.reload(true);
  }

  /**
   * Save access data in the storage
   *
   * @private
   * @param {AccessData} data
   */
  private saveAccessData({access_token, refreshToken}: AccessData) {
    this.tokenStorage
      .setAccessToken(access_token)
      .setRefreshToken(refreshToken);
  }

  register(user: User): Observable<any> {
    return this.http.put(environment.oauth_url + '/userRequest', user);
  }
}
