import {Component, OnInit} from '@angular/core';
import {UserService} from '../../../services/user.service';
import {LoadingService} from '../../../../core/services/loading.service';

@Component({
  selector: 'app-user-account-list',
  templateUrl: './user-account-list.component.html',
  styleUrls: ['./user-account-list.component.scss']
})
export class UserAccountListComponent implements OnInit {


  displayedColumns: string[] = [
    'title',
    'lastName',
    'firstName',
    'email',
    'center.name',
    'actions'];
  dataSourceToValidate = [];
  dataSourceValidated = [];

  constructor(private userService: UserService, private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.loadUsersInPending();
    this.loadUsers();
  }

  loadUsersInPending() {
    this.userService.getUsersInPending().subscribe(value => {
      this.loadingService.hide();
      this.dataSourceToValidate = value;
    });
    ;
  }

  loadUsers() {
    this.userService.getUsers().subscribe(value => {
      this.dataSourceValidated = value;
      this.loadingService.hide();
    });
  }

  validateRegister(id: string) {
    this.loadingService.show();
    this.userService.validateUser(id).subscribe(value => {
        this.loadUsersInPending();
        this.loadUsers();
      }
    );
  }

}
