import {Pipe, PipeTransform} from '@angular/core';
import {Patient} from '../model/model';
import * as moment from 'moment';


/**
 * Display patient
 * use arg 'birthDate' to display birthdate
 */
@Pipe({
  name: 'patient'
})
export class PatientPipe implements PipeTransform {

  transform(patient: Patient, args?: any): string {
    if (!patient) {
      return '';
    }
    const result = patient.birthName + (patient.lastName ? ' (' + patient.lastName + ')' : '') + ' ' + patient.firstName;
    if (args && args.indexOf('birthDate') > -1) {
      return result + ' (' + moment(patient.birthDate).format('L') + ')';
    }
    return result;
  }

}
