import {Component, Inject, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {LoadingService} from '../../../core/services/loading.service';
import {TranslateService} from '@ngx-translate/core';
import {RequestService} from '../../services/request.service';
import {Comment} from '../../../core/model/model';

export interface Data {
  comments: Array<Comment>;
}

@Component({
  selector: 'app-request-comments',
  templateUrl: './request-comments.component.html',
  styleUrls: ['./request-comments.component.scss']
})
export class RequestCommentsComponent implements OnInit {
  private data: Data;
  comments: Array<Comment> = [];
  form: FormGroup;
  errorMessage: string;

  constructor(public dialog: MatDialog, private translateService: TranslateService,
              private dialogRef: MatDialogRef<RequestCommentsComponent>,
              @Inject(MAT_DIALOG_DATA) data: Data) {
    this.data = data;
    if (data.comments) {
      this.comments = data.comments;
    }
  }

  ngOnInit() {
  }

  close() {
    this.dialogRef.close();
  }


}
