import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Center, User} from '../../core/model/model';
import {environment} from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient) {
  }


  getUsersInPending(): Observable<Array<User>> {
    return this.http.get(environment.oauth_url + '/userRequest/pending') as Observable<Array<User>>;
  }

  getUsers(): Observable<Array<User>> {
    return this.http.get(environment.oauth_url + '/user') as Observable<Array<User>>;
  }

  validateUser(userRequestId: string): Observable<Object> {
    return this.http.get(environment.oauth_url + '/userRequest/validate/' + userRequestId) as Observable<Object>;
  }
}
