import {Component, OnInit} from '@angular/core';
import {AuthenticationService} from '../services/authentication.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  form: FormGroup;
  formOtp: FormGroup;
  errorMessage: String;
  otp: boolean = false;

  constructor(private router: Router, private authenticationService: AuthenticationService, private fb: FormBuilder, private translate: TranslateService) {
  }

  ngOnInit() {
    this.form = this.fb.group({
      login: ['', Validators.required],
      password: ['', Validators.required]
    });

    this.formOtp = this.fb.group({
      otp: ['', Validators.required]
    });
  }


  getErrorMessage(field: string) {
    return this.form.get(field).hasError('required') ? this.translate.instant('form.field.required') :
      this.form.get(field).hasError('email') ? this.translate.instant('form.field.emailInvalid') :
        '';
    // return (
    //   (!this.form.get(field).valid && this.form.get(field).touched) ||
    //   (this.form.get(field).untouched && this.formSubmitAttempt)
    // );
  }

  onSubmit() {
    if (this.form.valid) {
      this.authenticationService.login(this.form.value.login, this.form.value.password).subscribe(
        value => this.router.navigateByUrl('/teleexpertise/requests'),
        error => {
          if (error.error.error_description.indexOf('OTP') !== -1) {
            this.otp = true;
            this.errorMessage = '';
          } else {
            this.errorMessage = error.error.error_description;
          }
        }
      );
    }
  }

  onSubmitOtp() {
    if (this.formOtp.valid) {
      this.authenticationService.login(this.form.value.login, this.form.value.password, this.formOtp.value.otp).subscribe(
        value => this.router.navigateByUrl('/teleexpertise/dashboard'),
        error => {
          this.errorMessage = error.error.error_description;
        }
      );
    }
  }

}
