import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {Patient, User} from '../model/model';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class PatientService {

  constructor(private http: HttpClient) {
  }


  getPatientsForCenter(centerId: string): Observable<Array<Patient>> {
    return this.http.get(environment.api_url + '/patient/byCenter/' + centerId) as Observable<Array<Patient>>;
  }

  getPatients(): Observable<Array<Patient>> {
    return this.http.get(environment.api_url + '/patient') as Observable<Array<Patient>>;
  }

  save(patient: Patient): Observable<any> {
    return this.http.put(environment.api_url + '/patient', patient);
  }
}
