import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CommonModule} from '@angular/common';
import {BackofficeComponent} from './backoffice.component';
import {UserAccountListComponent} from './pages/user-account/user-account-list/user-account-list.component';
import {ProtectedGuard} from 'ngx-auth';

const routes: Routes = [
  {
    path: 'backoffice', component: BackofficeComponent, canActivate: [ProtectedGuard],
    children: [
      {path: '', redirectTo: 'users', pathMatch: 'full'},
      {path: 'users', component: UserAccountListComponent}
    ]
  },

];


@NgModule({
  imports: [
    CommonModule, RouterModule.forRoot(routes)
  ],
  declarations: [],
  exports: [RouterModule]
})
export class BackofficeRoutingModule {
}
