export class Identifiable {
  id: string;
  creationDate: Date;
}

export class ValueSetEntry {
  code: string;
  vsCode: string;
}

export class Address {
  number: string;
  street: string;
  additional: string;
  postalBox: string;
  postalCode: string;
  city: string;
  cedex: string;
  location: Location;
  country: string;
}

export class Location {
  lat: number;
  lng: number;
}

export enum Profile {
  PHARMACOLOGIST, CLINICIAN
}

export class User extends Identifiable {
  title: ValueSetEntry;
  lastName: string;
  firstName: string;
  email: string;
  department: string;
  nationalId: string;
  phone: string;
  profile: Profile;
  center: Center = new Center();
  language: string;
  specialty: string;
  otpRequired: boolean;
}

export class Center extends Identifiable {
  name: string;
  code: string;
  contract: boolean;
  phone: string;
  vat: string;
  address: Address = new Address;
  fax: string;
}

export class HealthInsurance {
  registrationNumber: string;
  codeOrganisation: string;
}


export enum ConsentType {
  TELE_EXPERTISE = 'TELE_EXPERTISE', TELE_MONITORING = 'TELE_MONITORING'
}

export class Consent {
  startDate: Date;
  endDate: Date;
  type: ConsentType;
}

export class MedicalInformation {

  indications: Array<Indication> = [];
  otherIndications: Array<Indication> = [];
  measurements: Array<Measurement> = [];
  consents: Array<Consent> = [];
}

export enum BodyMeasurementType {
  WEIGHT, HEIGHT
}

export class ValueUnit {
  value: number;
  unit: string;
}

export class Measurement extends ValueUnit {
  code: string;
  date: Date;
}

export class Patient extends Identifiable {
  externalId: string;
  birthName: string;
  lastName: string;
  firstName: string;
  gender: ValueSetEntry;
  birthDate: string;
  email: string;
  birthRank: number;
  nationalId: string;
  address: Address;
  center: Center;
  healthInsurance: HealthInsurance = new HealthInsurance();
  medicalInformation: MedicalInformation = new MedicalInformation();
}

export enum RequestStatus {
  NEW, WAITING_TREATMENT, WAITING_VALIDATION, INFORMATION_REQUEST, VALIDATED, PRE_VALIDATED, CLOSED, ACCEPTED
}

export class RequestContext {
  reason: string;
  additionalInformation: string;
}

export class Treatment {
  drug: ValueSetEntry;
  drugCommercialName: ValueSetEntry;
  numberOfTakes: number;
  doses: Array<ValueUnit>;
  administrationRoute: ValueSetEntry;
  coTreatments: Array<ValueSetEntry>;
}


export class Sample {
  intervalDoseSampling: ValueUnit;
  realSamplingTime: string;
  optimalSamplingTime: number;
  concentration: ValueUnit;
}

export class BloodSample {
  samplingDate: Date;
  drugIntakeTime: string;
  assayMethod: ValueSetEntry;
  samples: Array<Sample>;
}

export class FormattedDate {
  date: string;
  format: string;
}

export class Indication extends ValueSetEntry {
  date: string;
  comment: string;
  mucoviscidose: boolean;
}

export class Comment {
  date: Date;
  content: string;
  author: User;
}

export class ValeurUnite {
  valeur: number;
  unite: string;
}

export class ValeursUnite {
  valeurs: Array<number>;
  unite: string;
}

export class Cinetic {
  C0: ValeurUnite;
  Cmax: ValeurUnite;
  Freq: number;
  AUC: ValeurUnite;
  TargetAUC: ValeursUnite;
  Poso: ValeursUnite;
  dose: ValeurUnite;
  AUCReg: ValeurUnite;
}

export class RequestResult {
  best: boolean;
  selected: boolean;
  log: string;
  grf: string;
  date: Date;
  txt: string;
  cinetic: Cinetic = new Cinetic();
}

export class RequestResultGroup {
  bloodSample: BloodSample;
  treatment: Treatment;
  date: Date;
  results: Array<RequestResult>;
}

export class Request extends Identifiable {
  code: number;
  status: RequestStatus;
  requester: User;
  prescriber: string;
  center: Center;
  patient: Patient;
  context: RequestContext;
  indication: Indication;
  otherIndications: Array<Indication>;
  treatment: Treatment;
  bloodSample: BloodSample;
  comments: Array<Comment>;
  resultGroups: Array<RequestResultGroup>;
}

export class PsInfo extends Identifiable {

  identificationNationalePp: string;
  nomExercice: string;
  prenomExercice: string;
  infos: Map<String, String>;

}






