import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {TranslateService} from '@ngx-translate/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {RequestService} from '../../services/request.service';
import {Comment} from '../../../core/model/model';
import {LoadingService} from '../../../core/services/loading.service';

export interface Data {
  requestId: string;
}

@Component({
  selector: 'app-request-ask-informations',
  templateUrl: './request-ask-informations.component.html',
  styleUrls: ['./request-ask-informations.component.scss']
})
export class RequestAskInformationsComponent implements OnInit {
  private data: Data;
  form: FormGroup;
  errorMessage: string;

  constructor(public dialog: MatDialog, private fb: FormBuilder, private loadingService: LoadingService,
              private translateService: TranslateService, private requestService: RequestService,
              private dialogRef: MatDialogRef<RequestAskInformationsComponent>,
              @Inject(MAT_DIALOG_DATA) data: Data) {
    this.data = data;
    this.form = this.fb.group({
      comment: ['', Validators.required]
    });
  }

  ngOnInit() {
  }

  close() {
    this.dialogRef.close();
  }

  onSubmit() {
    if (this.form.valid) {
      const comment = new Comment();
      comment.date = new Date();
      comment.content = this.form.get('comment').value;
      this.loadingService.show();
      this.requestService.askForInformation(this.data.requestId, comment).subscribe(
        result => {
          this.loadingService.hide();
          this.dialogRef.close(result);
        },
        error => {
          this.loadingService.hide();
          this.errorMessage = error.error.description;
        }
      );
    }
  }

}
