@Injectable({

  providedIn: 'root'
})
export class RequestService {

  constructor(private http: HttpClient) {
  }

  sendRequest(request: Request): Observable<any> {
    return this.http.put(environment.api_url + '/request', request);
  }

  getRequests(): Observable<Array<Request>> {
    return this.http.get(environment.api_url + '/request') as Observable<Array<Request>>;
  }


  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  }

  initRequest(patientId: string, centerId: string): Observable<Request> {
    return this.http.get(environment.api_url + '/request/init/' + patientId + '/' + centerId) as Observable<Request>;
    // return new Observable(subscriber => {
    //   const req = new Request();
    //   req.indication = new Indication();
    //   req.indication.code = 'T_LUNG';
    //   subscriber.next(req);
    //   subscriber.complete();
    // });
  }

  getRequest(id: string): Observable<Request> {
    return this.http.get(environment.api_url + '/request/' + id) as Observable<Request>;
  }

  askForInformation(requestId: string, comment: Comment): Observable<any> {
    return this.http.post(environment.api_url + '/request/' + requestId + '/comment', comment);
  }

  launchCalculation(requestId: string, populations: string[]): Observable<any> {
    return this.http.post(environment.api_url + '/request/' + requestId + '/launchCalculation', populations);
  }

  closeRequest(requestId: string, comment: Comment): Observable<any> {
    return this.http.post(environment.api_url + '/request/' + requestId + '/close', comment) as Observable<Request>;
  }

  validateRequest(requestId: string, comment: Comment): Observable<any> {
    return this.http.post(environment.api_url + '/request/' + requestId + '/validate', comment) as Observable<Request>;
  }

  acceptRequest(requestId: string, comment: Comment): Observable<any> {
    return this.http.post(environment.api_url + '/request/' + requestId + '/accept', comment) as Observable<Request>;
  }

  getPopulations(requestId: string): Observable<Array<any>> {
    return this.http.get(environment.api_url + '/population/forRequest/' + requestId) as Observable<Array<any>>;
  }
}
