import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ProtectedGuard, PublicGuard} from 'ngx-auth';
import {CommonModule} from '@angular/common';
import {TeleexpertiseComponent} from './teleexpertise.component';
import {DashboardComponent} from './pages/dashboard/dashboard.component';
import {RequestListComponent} from './pages/request/request-list/request-list.component';
import {PublicationComponent} from './pages/publication/publication.component';
import {HomeComponent} from './pages/home/home.component';
import {RequestCreateComponent} from './pages/request/request-create/request-create.component';
import {RequestEditComponent} from './pages/request/request-edit/request-edit.component';
import {PatientListComponent} from './pages/patient/patient-list/patient-list.component';
import {PatientCreateComponent} from './pages/patient/patient-create/patient-create.component';
import {PatientEditComponent} from './pages/patient/patient-edit/patient-edit.component';


const routes: Routes = [
  { path: '', redirectTo: '/teleexpertise/home', pathMatch: 'full'},
  { path: 'teleexpertise', component: TeleexpertiseComponent,
    children: [
      { path: '', redirectTo: 'home', pathMatch: 'full' },
      { path: 'home', component: HomeComponent },
      { path: 'dashboard', component: DashboardComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'requests', component: RequestListComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'requests/new', component: RequestCreateComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'requests/:id', component: RequestEditComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'patients', component: PatientListComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'patients/new', component: PatientCreateComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'patients/:id', component: PatientEditComponent,  canActivate: [ ProtectedGuard ] },
      { path: 'publication', component: PublicationComponent }
      ]
  },

];


@NgModule({
  imports: [
    CommonModule, RouterModule.forRoot(routes)
  ],
  declarations: [],
  exports: [RouterModule]
})
export class TeleexpertiseRoutingModule { }
