import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {PatientService} from '../../../../core/services/patient.service';
import {AuthenticationService} from '../../../../auth/services/authentication.service';
import {Patient} from '../../../../core/model/model';

@Component({
  selector: 'app-patient-list',
  templateUrl: './patient-list.component.html',
  styleUrls: ['./patient-list.component.scss']
})
export class PatientListComponent implements OnInit {


  dataSource = [];

  constructor(private patientService: PatientService, private router: Router, private authService: AuthenticationService) {
  }

  ngOnInit() {
    this.patientService.getPatients().subscribe(value => this.dataSource = value);
  }

  selectPatient(patient: Patient) {
    this.router.navigateByUrl('/teleexpertise/patients/' + patient.id);
  }

}


