import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Center} from '../model/model';
import {environment} from '../../../environments/environment';

const REQUESTS_DATA: any[] = [
    {
      name: 'Centre hospitalier universitaire de Limoges',
      code: '001',
      address: {
        number: '2',
        street: 'Avenue Martin Luther King',
        additional: '',
        postalBox: '',
        postalCode: '87000',
        city: 'Limoges',
        cedex: '',
        country: 'FRANCE'
      }
    }
  ]
;

@Injectable({
  providedIn: 'root'
})
export class CenterService {
  constructor(private http: HttpClient) {
  }


  getCenters(): Observable<Array<Center>> {
    return this.http.get(environment.oauth_url + '/center') as Observable<Array<Center>>;
  }
}
