import {NgModule, Optional, SkipSelf} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {RouterModule} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {NgxMaterialTimepickerModule} from 'ngx-material-timepicker';
import {
  MatAutocompleteModule,
  MatBadgeModule,
  MatButtonModule,
  MatCardModule,
  MatCheckboxModule, MatChipsModule, MatDatepickerModule,
  MatDialogModule,
  MatExpansionModule,
  MatFormFieldModule, MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule, MatNativeDateModule,
  MatPaginatorModule,
  MatProgressSpinnerModule,
  MatRadioModule,
  MatSelectModule,
  MatSidenavModule, MatSlideToggleModule,
  MatStepperModule,
  MatTableModule,
  MatTabsModule,
  MatTooltipModule
} from '@angular/material';
import {MatToolbarModule} from '@angular/material/toolbar';
import {FlexLayoutModule} from '@angular/flex-layout';
import {GooglePlaceService} from './services/google-place.service';
import {AgmCoreModule} from '@agm/core';
import {GooglePlaceDirective} from './directives/google-place.directive';
import {CenterService} from './services/center.service';
import {ConfirmDialogComponent} from './components/dialog/confirm-dialog/confirm-dialog.component';
import {CdkTableModule} from '@angular/cdk/table';
import {PsInfoService} from './services/ps-info.service';
import {LoadingComponent} from './components/loading/loading.component';
import {LoadingService} from './services/loading.service';
import {ConfigurationService} from './services/configuration.service';
import {MatMomentDateModule} from '@angular/material-moment-adapter';
import { PatientPipe } from './pipes/patient.pipe';
import { CenterPipe } from './pipes/center.pipe';
import { UserPipe } from './pipes/user.pipe';
import { VsePipe } from './pipes/vse.pipe';
import {ValueSetService} from './services/value-set.service';
import { DatetimePipe } from './pipes/datetime.pipe';
import { SpeedDialFabComponent } from './components/speed-dial-fab/speed-dial-fab.component';

@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatToolbarModule,
    MatIconModule,
    MatMenuModule,
    MatSidenavModule,
    MatCardModule,
    MatBadgeModule,
    MatTabsModule,
    MatTooltipModule,
    FlexLayoutModule,
    MatExpansionModule,
    MatListModule,
    MatProgressSpinnerModule,
    RouterModule,
    MatDialogModule,
    NgxMaterialTimepickerModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyByAVVURZudjBg5ncvzbjlDOqNz02ZwIw0',
      libraries: ['places']
    }),
  ],
  exports: [
    CommonModule,
    HttpClientModule,
    BrowserAnimationsModule,
    CdkTableModule,
    MatRadioModule,
    MatButtonModule,
    MatCheckboxModule,
    MatToolbarModule,
    MatIconModule,
    MatMenuModule,
    MatSidenavModule,
    MatCardModule,
    MatBadgeModule,
    MatTabsModule,
    MatFormFieldModule,
    MatInputModule,
    MatDatepickerModule,
    MatMomentDateModule,
    MatGridListModule,
    FlexLayoutModule,
    MatExpansionModule,
    MatTooltipModule,
    MatSlideToggleModule,
    MatTableModule,
    MatPaginatorModule,
    MatListModule,
    MatStepperModule,
    MatChipsModule,
    MatSelectModule,
    ReactiveFormsModule,
    TranslateModule,
    GooglePlaceDirective,
    MatDialogModule,
    ConfirmDialogComponent,
    FormsModule,
    MatAutocompleteModule,
    LoadingComponent,
    NgxMaterialTimepickerModule,
    CenterPipe,
    PatientPipe,
    UserPipe,
    VsePipe,
    DatetimePipe,
    SpeedDialFabComponent
  ],
  declarations: [
    GooglePlaceDirective, ConfirmDialogComponent, LoadingComponent, PatientPipe, CenterPipe, UserPipe, VsePipe, DatetimePipe, SpeedDialFabComponent
  ],
  providers: [
    GooglePlaceService,
    CenterService,
    PsInfoService,
    LoadingService,
    ConfigurationService,
    ValueSetService
  ]
})
export class CoreModule {

  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    // Import guard
    if (parentModule) {
      throw new Error(`${parentModule} has already been loaded. Import Core module in the AppModule only.`);
    }
  }
}

// required for AOT compilation
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

