import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {PsInfo} from '../model/model';
import {environment} from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PsInfoService {

  constructor(private http: HttpClient) {
  }


  getPsInfosByIdentifiant(id: string): Observable<Array<PsInfo>> {
    return this.http.get(environment.oauth_url + '/psInfo/startsWith/' + id) as Observable<Array<PsInfo>>;
  }
}
