import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {AuthenticationService} from '../services/authentication.service';
import {Address, Center, Location, PsInfo, User, ValueSetEntry} from '../../core/model/model';
import {GooglePlaceService} from '../../core/services/google-place.service';
import {CenterService} from '../../core/services/center.service';
import {MatDialog} from '@angular/material';
import {ConfirmDialogComponent} from '../../core/components/dialog/confirm-dialog/confirm-dialog.component';
import {Observable} from 'rxjs';
import {debounceTime, distinctUntilChanged, switchMap} from 'rxjs/operators';
import {PsInfoService} from '../../core/services/ps-info.service';
import {TranslateService} from '@ngx-translate/core';
import {UserService} from '../../backoffice/services/user.service';
import {LoadingService} from '../../core/services/loading.service';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  form: FormGroup;
  errorMessage: String;
  private place: google.maps.places.PlaceResult;
  centers: Array<Center>;
  titles = [{code: 'DR', vsCode: 'TITLE'},
    {code: 'PR', vsCode: 'TITLE'},
    {code: 'MR', vsCode: 'TITLE'},
    {code: 'MRS', vsCode: 'TITLE'}];
  userFrench = true;
  psInfosOptions: Observable<PsInfo[]>;
  nationalId: AbstractControl;
  centerPanelOpened = {value: false};

  constructor(private googlePlaceService: GooglePlaceService, private router: Router, private cdr: ChangeDetectorRef, public dialog: MatDialog,
              private authenticationService: AuthenticationService, private fb: FormBuilder, private centerService: CenterService,
              private psInfoService: PsInfoService, private translate: TranslateService, private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.form = this.fb.group({
      nationalId: ['', this.nationalIdRequired()],
      title: ['', Validators.required],
      lastName: ['', Validators.required],
      firstName: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      department: [''],
      center: this.fb.group({
        name: ['', this.centerRequired()],
        id: ['', this.centerRequired('id')],
        address: this.fb.group({
          addressComplete: [''],
          number: [''],
          street: [''],
          additional: [''],
          postalBox: [''],
          postalCode: ['', this.centerRequired()],
          city: ['', this.centerRequired()],
          cedex: [''],
          location: this.fb.group({
            lat: '',
            lng: '',
          }),
          country: ['', this.centerRequired()],
        }),
      })
    });

    this.loadData();

    this.nationalId = this.form.get('nationalId');
    this.psInfosOptions = this.nationalId.valueChanges
      .pipe(
        debounceTime(200),
        distinctUntilChanged(),
        switchMap(val => {
          return this.loadPsInfos(val || '');
        })
      );
  }

  centerRequired(name: string = null): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if ((this.centerPanelOpened.value && name !== 'id') || (!this.centerPanelOpened.value && name === 'id')) {
        return Validators.required(control);
      }
      return null;
    };
  }

  nationalIdRequired(name: string = null): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (this.userFrench) {
        return Validators.required(control);
      }
      return null;
    };
  }


  loadPsInfos(val: string): Observable<PsInfo[]> {
    return this.psInfoService.getPsInfosByIdentifiant(val);
  }


  loadData() {
    this.centerService.getCenters().subscribe(centers => {
      this.centers = centers;
    });
  }

  selectPs(ps: PsInfo) {
    if (ps) {
      let center;
      const centerName: string = ps.infos['column19'];
      if (centerName) {
        center = this.centers.find(cen => cen.name.toUpperCase() === centerName.toUpperCase());
      }
      if (center) {
        this.form.patchValue({center: {code: center.code}});
        this.centerPanelOpened.value = false;
      } else {
        const newUser: User = new User();
        if (!!ps.infos['column3']) {
          const title = ps.infos['column3'].toUpperCase() as string;
          newUser.title = new ValueSetEntry();
          newUser.title.code = title;
          newUser.title.vsCode = 'TITLE';
        }
        newUser.nationalId = ps.identificationNationalePp;
        newUser.lastName = ps.nomExercice;
        newUser.firstName = ps.prenomExercice;
        newUser.specialty = ps.infos['column12'];
        newUser.email = ps.infos['column39'];
        newUser.phone = ps.infos['column37'];
        const newCenter: Center = new Center();
        newCenter.name = centerName;
        newCenter.phone = ps.infos['column36'];
        newCenter.fax = ps.infos['column38'];
        newCenter.address = new Address();
        newCenter.address.number = ps.infos['column24'];
        newCenter.address.street = '';
        if (!!ps.infos['column27']) {
          newCenter.address.street = ps.infos['column27'];
        }
        if (!!ps.infos['column28']) {
          newCenter.address.street += ' ' + ps.infos['column28'];
          newCenter.address.street = newCenter.address.street.trim();
        }
        newCenter.address.additional = '';
        if (!!ps.infos['column22']) {
          newCenter.address.additional = ps.infos['column22'];
        }
        if (!!ps.infos['column23']) {
          newCenter.address.additional += ' ' + ps.infos['column23'];
          newCenter.address.additional = newCenter.address.additional.trim();
        }
        newCenter.address.postalBox = ps.infos['column29'];
        if (!!ps.infos['column30'] && ps.infos['column30'].indexOf('CEDEX') !== -1) {
          const cedex = ps.infos['column30'] as string;
          newCenter.address.cedex = cedex.substring(cedex.toUpperCase().indexOf('CEDEX'), cedex.length);
        }
        newCenter.address.postalCode = ps.infos['column31'];
        newCenter.address.city = ps.infos['column33'];
        newCenter.code = ps.infos['column15'] ? ps.infos['column15'] : ps.infos['column17'];
        newCenter.address.country = 'France';
        newUser.center = newCenter;
        this.form.patchValue(newUser);
        this.centerPanelOpened.value = true;
      }
    }
  }


  getErrorMessage(field: string) {
    return this.form.get(field).hasError('required') ? this.translate.instant('form.field.required') :
      this.form.get(field).hasError('email') ? this.translate.instant('form.field.emailInvalid') :
        '';
  }

  onSubmit() {
    this.validateAllFormFields(this.form);
    if (this.form.valid) {
      const user = this.form.getRawValue();
      this.loadingService.show();
      this.authenticationService.register(user as User).subscribe(
        value => {
          this.loadingService.hide();
          const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
              title: this.translate.instant('register.confirm.title'),
              content: this.translate.instant('register.confirm.content')
            }
          });

          dialogRef.afterClosed().subscribe(result => {
            this.router.navigateByUrl('/teleexpertise/dashboard');
          });
        },
        error => {
          this.loadingService.hide();
          this.errorMessage = error.error.message;
        }
      );
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.updateValueAndValidity({onlySelf: true});
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
        control.updateValueAndValidity({onlySelf: true});
      }
    });
    formGroup.updateValueAndValidity({onlySelf: true});
  }


  setAddressOnChange(place: google.maps.places.PlaceResult) {
    this.place = place;
    if (this.place) {
      // set latitude, longitude and zoom
      const address: Address = new Address();
      address.location = new Location();
      address.location.lat = this.googlePlaceService.latitude(this.place);
      address.location.lng = this.googlePlaceService.longitude(this.place);
      address.number = this.googlePlaceService.street_number(this.place);
      address.street = this.googlePlaceService.street(this.place);
      address.postalCode = this.googlePlaceService.postal_code(this.place);
      address.city = this.googlePlaceService.city(this.place);
      address.postalBox = this.googlePlaceService.post_box(this.place);
      const centerName = this.googlePlaceService.name(this.place);
      address.additional = this.googlePlaceService.floor(this.place);
      address.cedex = this.googlePlaceService.postal_town(this.place);
      address.country = this.googlePlaceService.country(this.place);
      address['adresseComplete'] = null;
      this.form.get('center').get('address').patchValue(address);
      this.form.get('center').get('name').patchValue(centerName);
    } else {
      this.form.get('center').get('address').get('location').setValue(new Location());
    }
    this.cdr.markForCheck();
  }


}
