module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\nbody {\n  margin: 0; }\n.login {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0; }\nimg {\n  max-width: 50%; }\n.leftDiv {\n  color: white;\n  text-align: center;\n  text-align: -webkit-center;\n  background: #699fd5 url('bg-login.png') no-repeat center center;\n  background-size: 100% 100%;\n  padding: 30px; }\n.leftDiv h1 {\n    color: white; }\n.login-form {\n  margin-bottom: 30px;\n  margin-top: 50px; }\n.login-form button {\n    width: 100%; }\n/* @2x Images (Pixel Ratio of 1.25+) */\n@media only screen and (-webkit-min-device-pixel-ratio: 1.25), only screen and (min-device-pixel-ratio: 1.25), only screen and (min-resolution: 1.25dppx) {\n  .leftDiv {\n    background-image: url('bg-login@2x.png'); } }\n/* @3x Images (Pixel Ratio of 2.25+) */\n@media only screen and (-webkit-min-device-pixel-ratio: 2.25), only screen and (min-device-pixel-ratio: 2.25), only screen and (min-resolution: 2.25dppx) {\n  .leftDiv {\n    background-image: url('bg-login@3x.png'); } }\n"