import {Component, Inject, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {LoadingService} from '../../../core/services/loading.service';
import {TranslateService} from '@ngx-translate/core';
import {RequestService} from '../../services/request.service';
import {Comment} from '../../../core/model/model';
import {Data} from '../request-ask-informations/request-ask-informations.component';

export interface Data {
  requestId: string;
}

@Component({
  selector: 'app-request-select-population',
  templateUrl: './request-select-population.component.html',
  styleUrls: ['./request-select-population.component.scss']
})
export class RequestSelectPopulationComponent implements OnInit {
  private data: Data;
  form: FormGroup;
  errorMessage: string;
  populations: any;

  constructor(public dialog: MatDialog, private fb: FormBuilder, private loadingService: LoadingService,
              private translateService: TranslateService, private requestService: RequestService,
              private dialogRef: MatDialogRef<RequestSelectPopulationComponent>,
              @Inject(MAT_DIALOG_DATA) data: Data) {
    this.data = data;
    this.form = this.fb.group({
      populations: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.requestService.getPopulations(this.data.requestId).subscribe(
      data => this.populations = data
    );
  }

  close() {
    this.dialogRef.close();
  }

  onSubmit() {
    if (this.form.valid) {

      this.loadingService.show();
      const populations = this.form.get('populations').value;
      this.requestService.launchCalculation(this.data.requestId, populations).subscribe(
        result => {
          this.loadingService.hide();
          this.dialogRef.close(result);
        },
        error => {
          this.loadingService.hide();
          this.errorMessage = error.error.description;
        }
      );
    }
  }

}
