import {AbstractControl, FormControl, Validators} from '@angular/forms';


export function RequiredIfOtherControlHasValue(otherControl: AbstractControl, otherControlValue: string) {
  let thisControl: FormControl;

  return function matchOtherValidate(control: FormControl) {


    // Initializing the validator.
    if (!thisControl) {
      thisControl = control;
      if (!otherControl) {
        throw new Error('requiredIfOtherFieldHasValue(): other control is not found');
      }
      otherControl.valueChanges.subscribe(() => {
        thisControl.updateValueAndValidity();
      });
    }

    if (!otherControl) {
      return null;
    }

    if (otherControl.value === otherControlValue) {
      return Validators.required(control);
    }

    return null;

  };
}
export function RequiredIfOtherFieldHasValue(otherControlName: string, otherControlValue: string) {

  let thisControl: FormControl;
  let otherControl: FormControl;

  return function matchOtherValidate(control: FormControl) {

    if (!control.parent) {
      return null;
    }

    // Initializing the validator.
    if (!thisControl) {
      thisControl = control;
      otherControl = control.parent.get(otherControlName) as FormControl;
      if (!otherControl) {
        throw new Error('requiredIfOtherFieldHasValue(): other control is not found in parent group');
      }
      otherControl.valueChanges.subscribe(() => {
        thisControl.updateValueAndValidity();
      });
    }

    if (!otherControl) {
      return null;
    }

    if (otherControl.value === otherControlValue) {
      return Validators.required(control);
    }

    return null;

  };

}
