import {Injectable} from '@angular/core';
import {ValueSetEntry} from '../model/model';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ValueSetService {

  constructor() {
  }

  values = {
    INDICATION: ['T_HEART', 'T_HEPATIC', 'T_LUNG', 'T_KIDNEY', 'T_MARROW', 'A_LUPUS', 'A_OTHER'],

    DRUG: [
      'NONE',
      'CICLOSPORIN',
      'CORTICOSTEROIDS',
      'EVEROLIMUS',
      'MMF',
      'SIROLIMUS',
      'TACROLIMUS'],


    DRUG_COMMERCIAL_NAME: [
      'ADVAGRAF',
      'CELLCEPT',
      'ENVARUS',
      'NEORAL',
      'PROGRAF',
      'OTHER'],


    NUMBER_OF_TAKES: [
      '0.25',
      '0.33',
      '0.5',
      '1',
      '2',
      '3',
      '4'],


    ROUTE_OF_ADMINISTRATION: [
      'DRINKABLE',
      'TABLET',
      'TABLET_DISPERSIBLE',
      'IV'],

    ASSAY_METHOD: [
      'ACMIA',
      'CMIA',
      'EMIT',
      'HPLC',
      'FPIA',
      'KIT_ROCHE',
      'LCMS',
      'MEIA',
      'MPA_T'],

    REASON: [
      'Reason1',
      'Reason2',
      'Reason3',
      'Reason4',
      'Reason5',
      'Reason6',
      'Reason7',
      'Reason8']


  };


  drugCommercialNamesTreatmentsLink = [
    {drug: 'CICLOSPORIN', drugCommercialNames: ['NEORAL', 'OTHER']},
    {drug: 'MMF', drugCommercialNames: ['CELLCEPT', 'OTHER']},
    {
      drug: 'TACROLIMUS',
      drugCommercialNames: ['ADVAGRAF', 'ENVARUS', 'PROGRAF', 'OTHER']
    }
  ];

  getValues(vsCode: string): Observable<Array<ValueSetEntry>> {
    const values = this.values[vsCode] as Array<any>;
    const result = values.map(value => {
      return {code: value, vsCode: vsCode} as ValueSetEntry;
    });
    return new Observable(subscriber => {
      subscriber.next(result);
      subscriber.complete();
    });
  }

  getDrugCommercialNames(code: string): Observable<Array<ValueSetEntry>> {
    const link = this.drugCommercialNamesTreatmentsLink.find(link => link.drug === code);
    let result = [];
    if (link) {


      result = link.drugCommercialNames.map(value => {
        return {code: value, vsCode: 'DRUG_COMMERCIAL_NAME'} as ValueSetEntry;
      });

    }
    return new Observable(subscriber => {
      subscriber.next(result);
      subscriber.complete();
    });
  }


}
