import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Center} from '../model/model';
import {environment} from '../../../environments/environment';
import {RequestConfiguration} from '../model/configurationModel';

@Injectable({
  providedIn: 'root'
})
export class ConfigurationService {
  constructor(private http: HttpClient) {
  }


  getRequestConfiguration(): Observable<RequestConfiguration> {
    return this.http.get(environment.api_url + '/requestConfiguration') as Observable<RequestConfiguration>;
  }
}

