import {Component, OnInit} from '@angular/core';
import {AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators} from '@angular/forms';
import {PatientService} from '../../../../core/services/patient.service';
import {Center, ConsentType, Indication, Patient, Request, Sample} from '../../../../core/model/model';
import {TranslateService} from '@ngx-translate/core';
import {StepperSelectionEvent} from '@angular/cdk/stepper/typings/stepper';
import {CenterService} from '../../../../core/services/center.service';
import {ConfigurationService} from '../../../../core/services/configuration.service';
import {AGE_GT18, RequestConfiguration, WILD_CARD} from '../../../../core/model/configurationModel';
import {getTimeFormat} from '../../../../core/utils/DateUtils';
import {RequiredIfOtherControlHasValue, RequiredIfOtherFieldHasValue} from '../../../../core/validators/requiredIfOtherFIeldHasValue';
import {ConfirmDialogComponent} from '../../../../core/components/dialog/confirm-dialog/confirm-dialog.component';
import {RequestService} from '../../../services/request.service';
import {LoadingService} from '../../../../core/services/loading.service';
import {MatDialog} from '@angular/material';
import {Router} from '@angular/router';
import * as moment from 'moment';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {PatientCreateComponent} from '../../patient/patient-create/patient-create.component';
import {ValueSetService} from '../../../../core/services/value-set.service';

@Component({
  selector: 'app-request-new',
  templateUrl: './request-create.component.html',
  styleUrls: ['./request-create.component.scss']
})
export class RequestCreateComponent implements OnInit {

  errorMessage: String;
  treatmentFormGroup: FormGroup;
  patientFormGroup: FormGroup;
  indicationFormGroup: FormGroup;
  contextFormGroup: FormGroup;
  centerFormGroup: FormGroup;
  samplingFormGroup: FormGroup;
  dosesCtrl: FormArray;
  samplesCtrl: FormArray;
  treatmentStepLabel = '';
  patientStepLabel = '';
  contextStepLabel = '';
  centerStepLabel = '';
  indicationStepLabel = '';
  samplingStepLabel = '';
  numberOfTakes = 1;
  indicationDateLabel = '';
  timeFormat = getTimeFormat();

  indications = [];
  drugs = [];
  drugCommercialNamesFiltered = [];
  numberOfTakesValues = [];
  routes = [];
  assayMethod = [];
  reasons = [];

  patients: Array<Patient> = [];
  filteredPatients: Observable<Patient[]>;
  centers: Array<Center>;
  configuration: RequestConfiguration;
  minSample = 3;
  private center: Center;

  constructor(private _formBuilder: FormBuilder, private patientService: PatientService, private translateService: TranslateService,
              private centerService: CenterService, private configurationService: ConfigurationService,
              private requestService: RequestService, private loadingService: LoadingService,
              private translate: TranslateService, public dialog: MatDialog, private router: Router, private valueSetService: ValueSetService) {

    translateService.get('request.create.treatmentStepTitle').subscribe((text: string) => {
      this.treatmentStepLabel = this.translateService.instant('request.create.treatmentStepTitle');
      this.patientStepLabel = this.translateService.instant('request.create.patientStepTitle');
      this.indicationStepLabel = this.translateService.instant('request.create.indicationStepTitle');
      this.contextStepLabel = this.translateService.instant('request.create.contextStepTitle');
      this.centerStepLabel = this.translateService.instant('request.create.centerStepTitle');
      this.samplingStepLabel = this.translateService.instant('request.create.samplingStepTitle');
    });
  }

  ngOnInit() {
    this.centerFormGroup = this._formBuilder.group({
      center: [null, Validators.required],
      prescriber: [null, Validators.required]
    });
    this.indicationFormGroup = this._formBuilder.group({
      code: [null, Validators.required],
      vsCode: ['INDICATION', Validators.required],
      date: [null, Validators.required],
      comment: ['', RequiredIfOtherFieldHasValue('code', 'A_OTHER')],
      mucoviscidose: ['', RequiredIfOtherFieldHasValue('code', 'T_LUNG')],
    });
    this.treatmentFormGroup = this._formBuilder.group({
      drug: [null, Validators.required],
      drugCommercialName: [null],
      numberOfTakes: [null, Validators.required],
      administrationRoute: [null, RequiredIfOtherControlHasValue(this.indicationFormGroup.get('code'), 'T_MARROW')],
      coTreatments: [null],
      doses: this._formBuilder.array([]),
    });
    this.dosesCtrl = this.treatmentFormGroup.get('doses') as FormArray;
    this.patientFormGroup = this._formBuilder.group({
      patient: [null, this.patientRequired()]
    });
    this.contextFormGroup = this._formBuilder.group({
      reason: [null, Validators.required],
      additionalInformation: [null]
    });
    this.samplingFormGroup = this._formBuilder.group({
      drugIntakeTime: [null, Validators.required],
      samplingDate: [null, Validators.required],
      assayMethod: [null, Validators.required],
      samples: this._formBuilder.array([]),
      consent: [false, Validators.required],
    });
    this.samplesCtrl = this.samplingFormGroup.get('samples') as FormArray;
    this.samplesCtrl.push(this.createSample(true));
    this.samplesCtrl.push(this.createSample(true));
    this.samplesCtrl.push(this.createSample(true));
    this.loadData();

    this.filteredPatients = this.patientFormGroup.get('patient').valueChanges
      .pipe(
        startWith<string | Patient>(''),
        map(value => typeof value === 'string' ? value : this.getPatientLabel(value)),
        map(name => name ? this._filterPatient(name) : this.patients.slice())
      );

  }

  patientRequired(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const value = control.value;
      if (value && !value.birthName && !value.lastName) {
        return {patientRequired: true};
      }
      return Validators.required(control);
    };
  }


  private loadData() {
    this.centerService.getCenters().subscribe((centers) => this.centers = centers);
    this.configurationService.getRequestConfiguration().subscribe((config) => this.configuration = config);

    this.valueSetService.getValues('DRUG').subscribe(values => this.drugs = values);
    this.valueSetService.getValues('INDICATION').subscribe(values => this.indications = values);
    this.valueSetService.getValues('NUMBER_OF_TAKES').subscribe(values => this.numberOfTakesValues = values);
    this.valueSetService.getValues('ROUTE_OF_ADMINISTRATION').subscribe(values => this.routes = values);
    this.valueSetService.getValues('ASSAY_METHOD').subscribe(values => this.assayMethod = values);
    this.valueSetService.getValues('REASON').subscribe(values => this.reasons = values);
  }

  /**
   * Autocomplete filter for patient
   * @param name
   * @private
   */
  private _filterPatient(name: any) {
    const filterValue = name.toLocaleLowerCase();
    const tokens = filterValue.split(' ');
    return this.patients.filter(patient => {
      const patientLabel = this.getPatientLabel(patient).toLocaleLowerCase();
      const tokensMatch = tokens.filter(token => patientLabel.indexOf(token) > -1);
      return tokensMatch && tokensMatch.length === tokens.length;
    });
  }


  // ***********************************************************
  // ************************ Submit ***************************
  // ***********************************************************
  submit() {
    this.errorMessage = '';
    const request = new Request();
    if (this.samplingFormGroup.valid) {
      request.center = new Center();
      request.center.id = this.centerFormGroup.getRawValue().center.id;
      request.prescriber = this.centerFormGroup.getRawValue().prescriber;
      request.patient = new Patient();
      request.patient.id = this.patientFormGroup.getRawValue().patient.id;
      request.context = this.contextFormGroup.getRawValue();
      request.treatment = this.treatmentFormGroup.getRawValue();
      request.indication = this.indicationFormGroup.getRawValue();
      if (request.indication.code === 'T_LUMP' && request.indication['muscoviscidose']) {
        const ind = new Indication();
        ind.code = 'MUCOVISCIDOSE';
        ind.vsCode = 'OTHER_INDICATION';
        request.otherIndications.push(ind);
      }
      delete request.indication['muscoviscidose'];
      request.bloodSample = this.samplingFormGroup.getRawValue();
      delete request.bloodSample['consent'];
      this.loadingService.show();
      this.requestService.sendRequest(request).subscribe(
        value => {
          this.loadingService.hide();
          const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
              title: this.translate.instant('request.confirm.title'),
              content: this.translate.instant('request.confirm.content')
            }
          });

          dialogRef.afterClosed().subscribe(result => {
            this.router.navigateByUrl('/teleexpertise/requests');
          });
        },
        error => {
          this.loadingService.hide();
          this.errorMessage = error.error.message;
        }
      );
    }

  }

  // ***********************************************************
  // ******************* Change Handlers ***********************
  // ***********************************************************


  stepChange(event: StepperSelectionEvent) {
    if (event.previouslySelectedIndex === 0) {
      const cen: Center = this.centerFormGroup.get('center').value;
      this.centerStepLabel = cen.name + ' - ' + this.centerFormGroup.get('prescriber').value;
    } else if (event.previouslySelectedIndex === 1) {
      const pat: Patient = this.patientFormGroup.get('patient').value;
      this.patientStepLabel = this.getPatientLabel(pat);
    } else if (event.previouslySelectedIndex === 2) {
      this.indicationStepLabel = this.translate.instant(this.indicationFormGroup.get('vsCode').value + '.' + this.indicationFormGroup.get('code').value) +
        ' - ' + this.indicationDateLabel + ': ' + moment(this.indicationFormGroup.get('date').value).format('L');
    } else if (event.previouslySelectedIndex === 3) {
      this.treatmentStepLabel = this.treatmentFormGroup.get('drug').value.code +
        ' - ' + this.translateService.instant('request.treatment.numberOfTakes') + ': ' + this.treatmentFormGroup.get('numberOfTakes').value.code;
      if (this.treatmentFormGroup.get('administrationRoute').value) {
        this.treatmentStepLabel += ' - ' + this.translateService.instant('request.treatment.administrationRoute') +
          ': ' + this.treatmentFormGroup.get('administrationRoute').value.code;
      }
    } else if (event.previouslySelectedIndex === 4) {
      this.contextStepLabel = this.contextFormGroup.get('reason').value.code;
    }

    if (event.selectedIndex === 0) {
      this.centerStepLabel = this.translateService.instant('request.create.centerStepTitle');
    } else if (event.selectedIndex === 1) {
      this.patientStepLabel = this.translateService.instant('request.create.patientStepTitle');
    } else if (event.selectedIndex === 2) {
      this.indicationStepLabel = this.translateService.instant('request.create.indicationStepTitle');
    } else if (event.selectedIndex === 3) {
      this.treatmentStepLabel = this.translateService.instant('request.create.treatmentStepTitle');
    } else if (event.selectedIndex === 4) {
      this.contextStepLabel = this.translateService.instant('request.create.contextStepTitle');
    } else if (event.selectedIndex === 5) {
      this.handleSampleConfiguration();
    }
  }

  getPatientLabel(pat?: Patient) {
    return pat ? pat.birthName + (pat.lastName ? ' (' + pat.lastName + ')' : '') + ' ' + pat.firstName + ' (' + moment(pat.birthDate).format('L') + ')' : '';
  }

  indicationChange(code: string) {
    if (code.startsWith('T')) {
      this.indicationDateLabel = this.translateService.instant('request.indication.transplantation.date');
    } else {
      this.indicationDateLabel = this.translateService.instant('request.indication.treatment.date');
    }
  }

  drugChange(code: string) {
    this.drugCommercialNamesFiltered = [];
    this.valueSetService.getDrugCommercialNames(code).subscribe(values => this.drugCommercialNamesFiltered = values);
  }

  centerChange(center: Center) {
    this.center = center;
    this.patientService.getPatientsForCenter(center.id).subscribe((patients) => {
      this.patients = patients;
      this.patientFormGroup.get('patient').setValue('');
    });
  }

  patientChange(pat: Patient) {
    this.requestService.initRequest(pat.id, this.centerFormGroup.get('center').value.id).subscribe((request) => {
      if (request.treatment) {
        this.treatmentFormGroup.patchValue(request.treatment);
      }
      if (request.indication) {
        this.indicationFormGroup.patchValue(request.indication);
        this.indicationChange(request.indication.code);
      }
      this.samplingFormGroup.get('consent').setValue(false);
      if (request.patient.medicalInformation && request.patient.medicalInformation.consents) {
        request.patient.medicalInformation.consents.forEach(consent => {
          if (consent.type === ConsentType.TELE_EXPERTISE && (!consent.endDate || moment(consent.endDate).isAfter(moment()))) {
            this.samplingFormGroup.get('consent').setValue(true);
          }
        });
      }
    });
  }

  numberOfTakesChange(code: string) {
    this.dosesCtrl.controls = [];
    this.numberOfTakes = parseInt(code, 10);
    if (this.numberOfTakes < 1) {
      this.numberOfTakes = 1;
    }
    for (let i = 0; i < this.numberOfTakes; i++) {
      this.dosesCtrl.push(this.createDose());
    }
  }

  calculateTime(ctrl: AbstractControl, time: string = null) {
    const item: Sample = ctrl.value;
    const drugIntakeTime = time ? time : this.samplingFormGroup.get('drugIntakeTime').value;
    if (drugIntakeTime && item && item.intervalDoseSampling && item.intervalDoseSampling.value) {
      const currentHour = moment(drugIntakeTime, 'LT');
      const realSamplingTime = currentHour.add(item.intervalDoseSampling.value, 'minutes').format('LT');
      ctrl.patchValue({realSamplingTime: realSamplingTime});
    }
  }

  drugIntakeTimePickerChange(time: string) {
    this.samplesCtrl.controls.forEach(ctrl => this.calculateTime(ctrl, time));
  }


  // ***********************************************************
  // *******************FormGroup creator***********************
  // ***********************************************************

  createDose(): FormGroup {
    return this._formBuilder.group({value: ['', Validators.required], unit: ['mg']});
  }

  createSample(required: boolean): FormGroup {
    return this._formBuilder.group({
      intervalDoseSampling: this._formBuilder.group({
        value: ['', Validators.required],
        unit: ['min']
      }),
      realSamplingTime: ['', Validators.required],
      optimalSamplingTime: ['', Validators.required],
      concentration: this._formBuilder.group({
        value: ['', Validators.required],
        unit: ['mg/l']
      })
    });
  }

  addSample(): void {
    this.samplesCtrl.push(this.createSample(false));
  }

  removeSample(index: number): void {
    this.samplesCtrl.removeAt(index);
  }

  // ***********************************************************
  // *****************createPatient*********************
  // ***********************************************************
  createPatient() {
    const dialogRef = this.dialog.open(PatientCreateComponent, {data: {center: this.center}});

    dialogRef.afterClosed().subscribe(patient => {
      if (patient) {
        this.patientService.getPatientsForCenter(this.center.id).subscribe((patients) => {
          this.patients = patients;
          this.patientFormGroup.get('patient').setValue(patient);
        });
      }
    });
  }


  // ***********************************************************
  // *****************Request Configuration*********************
  // ***********************************************************

  handleSampleConfiguration() {
    let indication = this.configuration.sampling.indications.find(ind => ind.indication === this.indicationFormGroup.get('code').value);
    if (!indication) {
      indication = this.configuration.sampling.indications.find(ind => ind.indication === WILD_CARD);
    }
    let age = indication.ages.find(ageValue => ageValue.age === AGE_GT18);
    if (!age) {
      age = indication.ages.find(ageValue => ageValue.age === WILD_CARD);
    }
    let disease = age.diseases.find(dis => dis.disease === WILD_CARD);
    if (!disease) {
      disease = age.diseases.find(dis => dis.disease === WILD_CARD);
    }
    let routeOfAdministration;
    if (this.treatmentFormGroup.get('administrationRoute').value) {
      routeOfAdministration = disease.roas.find(roa => roa.roa === this.treatmentFormGroup.get('administrationRoute').value.code);
    }
    if (!routeOfAdministration) {
      routeOfAdministration = disease.roas.find(roa => roa.roa === WILD_CARD);
    }
    let drug = routeOfAdministration.drugs.find(dr => dr.drug === this.treatmentFormGroup.get('drug').value.code);
    if (!drug) {
      drug = routeOfAdministration.drugs.find(dr => dr.drug === WILD_CARD);
    }
    let drugCommercialName;
    if (this.treatmentFormGroup.get('drugCommercialName').value) {
      drugCommercialName = drug.drugCommercialNames.find(dcn => dcn.drugCommercialName === this.treatmentFormGroup.get('drugCommercialName').value.code);
    }
    if (!drugCommercialName) {
      drugCommercialName = drug.drugCommercialNames.find(dr => dr.drugCommercialName === WILD_CARD);
    }
    const sampling: Array<string> = drugCommercialName.sampling;
    this.minSample = Math.min(3, sampling.length);
    for (let i = 0; i < sampling.length; i++) {
      let control = this.samplesCtrl.controls[i];
      if (!control) {
        this.samplesCtrl.push(this.createSample(false));
      }
      control = this.samplesCtrl.controls[i];
      control.patchValue({optimalSamplingTime: sampling[i]});
    }
    while (this.samplesCtrl.controls.length > sampling.length) {
      this.samplesCtrl.removeAt(this.samplesCtrl.controls.length - 1);
    }

  }

}
