import {Component, OnInit} from '@angular/core';
import {RequestService} from '../../../services/request.service';
import {Router} from '@angular/router';
import {Request} from '../../../../core/model/model';

@Component({
  selector: 'app-request',
  templateUrl: './request-list.component.html',
  styleUrls: ['./request-list.component.scss']
})
export class RequestListComponent implements OnInit {

  displayedColumns: string[] = [
    'code',
    'status',
    'patient.lastName',
    'indication',
    'treatment.drug',
    'assayMethod',
    'indication.date',
    'creationDate'];
  dataSource = [];

  constructor(private requestService: RequestService, private router: Router) {
  }

  ngOnInit() {
    this.requestService.getRequests().subscribe(value => this.dataSource = value);

    // this.requestService.getResources().subscribe(obj => console.log(JSON.stringify(obj)));
  }

  selectRequest(request: Request) {
    this.router.navigateByUrl('/teleexpertise/requests/' + request.id);
  }

}
