import {Pipe, PipeTransform} from '@angular/core';
import {User, ValueSetEntry} from '../model/model';

@Pipe({
  name: 'vse'
})
export class VsePipe implements PipeTransform {

  transform(vse: ValueSetEntry, args?: any): string {
    if (!vse) {
      return '';
    }
    return vse.vsCode + '.' + vse.code;
  }

}
