import {Component, Input, OnInit} from '@angular/core';
import {RequestResult, RequestResultGroup} from '../../../core/model/model';

@Component({
  selector: 'app-request-result',
  templateUrl: './request-result.component.html',
  styleUrls: ['./request-result.component.scss']
})
export class RequestResultComponent implements OnInit {

  @Input('resultGroup') resultGroup: RequestResultGroup;
  result: RequestResult = new RequestResult();

  constructor() {
  }

  ngOnInit() {
    if (this.resultGroup) {
      this.result = this.resultGroup.results.find(result => result.selected);
    }
  }

}
